html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-size: 1rem;
  font-family: "Montserrat", sans-serif;
  color: #333333;
  line-height: 1.6;
}

.container {
  width: 100%;
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/*=================== Partials ====================*/
/*==================== Header =====================*/
.header {
  background: url("../img/header_bg.jpg") center no-repeat;
  background-size: cover;
}

.header-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 820px;
  width: 100%;
  height: 100vh;
}

.topline {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 15px;
  padding: 16px 0;
  width: 100%;
  transition: all .1s linear;
}

.topline--active {
  background: rgba(243, 129, 129, 0.9);
}

.topline--red {
  background: rgba(243, 129, 129, 0.9);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}

.logo {
  color: #fff;
  font-weight: 700;
  font-size: 1.875rem;
}

@media (max-width: 1200px) {
  .logo {
    font-size: calc(1.3125rem + 0.75vw) ;
  }
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.nav__button {
  color: #fff;
  transition: color .2s linear;
}

.nav__button:hover {
  color: #fce38a;
}

.menu {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 0.875rem;
}

.menu__item {
  display: inline-block;
  margin: 0 25px 0 25px;
}

.menu__item--active .menu__link {
  color: #fce38a;
}

.menu__item::after {
  width: 100%;
  opacity: 1;
}

.menu__link {
  color: #fff;
  transition: color .2s linear;
}

.menu__link::after {
  display: block;
  width: 0;
  height: 3px;
  background: #fce38a;
  content: '';
  opacity: 0;
  transition: all .2s linear;
}

.menu__link:hover {
  color: #fce38a;
}

.menu__link:hover::after {
  width: 100%;
  opacity: 1;
}

.nav-burger {
  position: absolute;
  top: 24px;
  right: 15px;
  z-index: 9999;
  display: none;
  padding: 10px 0;
  border: 0;
  background: none;
  color: transparent;
  font-size: 0;
  cursor: pointer;
}

.nav-burger:hover .nav-burger__line,
.nav-burger:hover .nav-burger__line::before,
.nav-burger:hover .nav-burger__line::after {
  background: #fce38a;
}

.nav-burger__line {
  position: relative;
  display: block;
  width: 30px;
  height: 3px;
  background: #fff;
  transition: all .2s linear;
}

.nav-burger__line::before, .nav-burger__line::after {
  position: absolute;
  left: 0;
  display: block;
  width: 30px;
  height: 3px;
  background: #fff;
  content: '';
  transition: all .2s linear;
}

.nav-burger__line::before {
  top: -10px;
}

.nav-burger__line::after {
  top: 10px;
}

.nav-burger--active:hover .nav-burger__line {
  background: none;
}

.nav-burger--active:hover .nav-burger__line::before,
.nav-burger--active:hover .nav-burger__line::after {
  background: #fce38a;
}

.nav-burger--active .nav-burger__line {
  background: none;
}

.nav-burger--active .nav-burger__line::before {
  transform: rotate(45deg);
  transform-origin: left top;
  transition: transform .2s linear;
}

.nav-burger--active .nav-burger__line::after {
  transform: rotate(-45deg);
  transform-origin: left bottom;
  transition: transform .2s linear;
}

/*================ Top Slider =================*/
.header-slider {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 80px;
}

.header-slider .slick-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(100vh - 80px - 91px);
}

.slide {
  margin: 0 auto;
  max-width: 840px;
  color: #fff;
