  height: 3px;
  background: #fff;
  content: '';
}

.slide__button {
  display: inline-block;
  margin-top: 50px;
  padding: 9px 32px;
  border: 3px solid #fff;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  transition: all .3s linear;
  font-size: 0.875rem;
}

.slide__button:hover {
  background: #fff;
  color: #333;
}

.slider-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.slider-nav__btn {
  position: relative;
  width: 23%;
  padding: 25px 0;
  border-top: 3px solid #fff;
  color: #fff;
  text-transform: uppercase;
  opacity: .7;
  cursor: pointer;
  transition: opacity .2s linear;
  font-size: 1.125rem;
}

.slider-nav__btn::before {
  position: absolute;
  top: -3px;
  left: 0;
  display: block;
  width: 0;
  height: 3px;
  background-color: #f38181;
  content: '';
  transition: all .2s linear;
}

.slider-nav__btn:hover {
  opacity: 1;
}

.slider-nav__btn:hover::before {
  width: 70px;
}

.slider-nav__btn--active {
  opacity: 1;
}

.slider-nav__btn--active::before {
  width: 70px;
}

.slider-nav__num {
  font-weight: 700;
  font-size: 1.5rem;
}

@media (max-width: 1200px) {
  .slider-nav__num {
    font-size: calc(1.275rem + 0.3vw) ;
  }
}

