/*==================== reviews ====================*/
.reviews {
  background: #f8f8f8;
}

.reviews__slider {
  position: relative;
}

.reviews__slide {
  display: flex;
  align-items: center;
  padding: 70px 30px;
}

.reviews__photo {
  display: block;
  margin: 0 60px;
  width: 147px;
  height: 147px;
  border: 3px solid #95e1d3;
}

.reviews__photo--noborder {
  width: 210px;
  height: 210px;
  border-radius: 50%;
  border: 0;
}

.reviews__review {
  width: 90%;
}

.reviews__text {
  padding: 0 15px;
  color: #999999;
  font-family: "Roboto", sans-serif;
  font-size: 1.5rem;
  font-weight: 300;
  font-style: italic;
  line-height: 36px;
}

@media (max-width: 1200px) {
  .reviews__text {
    font-size: calc(1.275rem + 0.3vw) ;
  }
}

.reviews__author {
  display: block;
  margin-top: 5px;
  color: #333333;
  font-family: "Kaushan Script", cursive;
  font-size: 1.5rem;
}

@media (max-width: 1200px) {
  .reviews__author {
    font-size: calc(1.275rem + 0.3vw) ;
  }
}

.reviews__author::before {
  content: '';
  display: inline-block;
  margin-right: 10px;
  vertical-align: middle;
  width: 60px;
  height: 3px;
  background-color: #f38181;
}

.slick-arrow {
  position: absolute;
  top: 50%;
  margin-top: -9px;
  content: '';
  display: block;
  width: 17px;
  height: 17px;
  font-size: 0;
  border: none;
  background: transparent;
  border-left: 3px solid #cccccc;
  border-top: 3px solid #cccccc;
  z-index: 888;
}

.slick-prev {
  left: 0;
  margin-left: 25px;
  transform: rotate(-45deg);
}

.slick-next {
  right: 0;
  margin-right: 25px;
  transform: rotate(135deg);
}

/*================== logos ================*/
.logos {
  background-color: #f8f8f8;
}

.logos__inner {
  padding: 40px 0 20px 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 -10px;
}

.logos__item {
  padding: 0 10px;
}

.logos__image {
  display: block;
  max-width: 100%;
  height: auto;
}

/*=============== works ==================*/
.works {
  display: flex;
  justify-content: center;
}

.works__column {
  display: flex;
  flex-direction: column;
}

.works__item {
  position: relative;
  flex: 1 1 0;
  background-color: #95e1d3;
}

.works__item--active {
  background: linear-gradient(to top, #fce38a 0%, #f38181 100%);
}

.works__item--active .works__image {
  opacity: .2;
}

.works__item--active .works__info {
  opacity: 1;
}

.works__item:hover {
  background: linear-gradient(to top, #fce38a 0%, #f38181 100%);
}

.works__item:hover .works__image {
  opacity: .2;
}

.works__item:hover .works__info {
  opacity: 1;
}

.works__image {
  display: block;
  max-width: 100%;
  height: auto;
  opacity: 1;
  transition: all .2s linear;
}

.works__info {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  color: #ffffff;
  text-align: center;
  opacity: 0;
  transition: all .2s linear;
}

.works__info svg {
  font-size: 1.875rem;
}

@media (max-width: 1200px) {
  .works__info svg {
    font-size: calc(1.3125rem + 0.75vw) ;
  }
}

.works__title {
  font-size: 0.875rem;
  text-transform: uppercase;
}

.works__text {
  font-size: 0.9375rem;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: italic;
  line-height: 24px;
}

/*=============== clients ==================*/
.clients {
  display: flex;
  flex-wrap: wrap;
}

.clients__item {
  display: flex;
  justify-content: flex-start;
  width: 50%;
  padding: 30px;
}

.clients__photo {
  width: 110px;
  height: 110px;
  border-radius: 50%;
}

.clients__content {
  max-width: 365px;
  padding-left: 30px;
}

.clients__title::after {
  content: '';
  display: block;
  margin: 17px 0;
  width: 60px;
  height: 3px;
  background-color: #f38181;
}

.clients__name {
  padding-top: 7px;
  color: #333333;
  font-size: 0.875rem;
  text-transform: uppercase;
}

.clients__post {
  padding-top: 5px;
  color: #333333;
  font-family: "Roboto", sans-serif;
  font-size: 0.9375rem;
  font-weight: 300;
  font-style: italic;
  line-height: 24px;
}

.clients__text {
  color: #999999;
  font-family: "Roboto", sans-serif;
  font-size: 0.9375rem;
  font-weight: 400;
  line-height: 24px;
}

/*============== blog ================*/
.blog {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 -15px;
}

.blog__item {
  display: flex;
  flex-direction: column;
  padding: 15px;
  width: 33.3333%;
}

.blog__item:hover .blog__title {
  text-decoration: underline;
  color: #f38181;
}

.blog__picture {
  position: relative;
}

.blog__img {
  display: block;
  max-width: 100%;
  height: auto;
}

.blog__date {
  position: absolute;
  top: 60%;
  left: -10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 70px;
  padding: 3px 15px;
  color: #ffffff;
  background-color: #95e1d3;
}

.blog__day {
  font-size: 1.875rem;
  font-weight: 700;
  text-transform: uppercase;
}

@media (max-width: 1200px) {
  .blog__day {
    font-size: calc(1.3125rem + 0.75vw) ;
  }
}

.blog__month {
  margin-top: -10px;
  font-size: 0.9375rem;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: italic;
}

.blog__title {
  padding-top: 8px;
  color: #333333;
  font-size: 0.875rem;
  font-weight: 400;
  text-transform: uppercase;
  transition: all .2s linear;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.blog__text {
  padding: 8px 0 13px 0;
  max-width: 100%;
  color: #999999;
  font-family: "Roboto", sans-serif;
  font-size: 0.9375rem;
  font-weight: 400;
  line-height: 24px;
  border-bottom: 1px solid #e5e5e5;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.blog__counters {
  padding-top: 13px;
  display: flex;
  justify-content: flex-start;
}

.blog__views, .blog__comments {
  padding: 0 20px 0 0;
}

.blog__views svg,
.blog__comments svg {
  font-size: 0.75rem;
  color: #95e1d3;
}

.blog__num {
  padding-left: 7px;
  color: #999999;
  font-family: "Roboto", sans-serif;
  font-style: italic;
  font-size: 0.9375rem;
  font-weight: 300;
  line-height: 24px;
}

/*=============== map ================*/
.map {
  display: flex;
  justify-content: center;
  text-align: center;
}

.map__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 140px;
  font-size: 1.5rem;
  font-weight: 700;
  text-transform: uppercase;
  transition: all .2s linear;
  color: #f38181;
}

@media (max-width: 1200px) {
  .map__link {
    font-size: calc(1.275rem + 0.3vw) ;
  }
}

.map__link--active {
  color: #333333;
}

.map__link--active .map__title::after {
  background-color: #333333;
}

.map__link:hover {
  color: #333333;
}

.map__link:hover .map__title::after {
  background-color: #333333;
}

.map__title::after {
  content: '';
  display: block;
  margin: 20px auto 0;
  width: 60px;
  height: 3px;
  background-color: #f38181;
  transition: all .2s linear;
}

.map-wrapper {
  width: 100%;
  height: 0;
  z-index: 99;
  transition: all .2s linear;
}

.map-wrapper--active {
  height: 50%;
}

#map {
  height: 100%;
}

/*================ footer ==================*/
.footer {
  padding: 75px 0 0 0;
  background: #f8f8f8;
}

.footer__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 70px;
}

.footer__col--left {
  width: 40%;
}

.footer__col--middle {
  width: 29%;
}

.footer__col--right {
  width: 23%;
}

.footer__logo {
  color: #cccccc;
  font-size: 2.875rem;
  font-weight: 700;
}

@media (max-width: 1200px) {
  .footer__logo {
    font-size: calc(1.4125rem + 1.95vw) ;
  }
}

.footer__text {
  margin: 40px 0;
  color: #999999;
  font-family: "Roboto", sans-serif;
  font-size: 0.9375rem;
  font-weight: 400;
  line-height: 24px;
}

.footer__followers {
  padding-bottom: 12px;
  border-bottom: 1px solid #e5e5e5;
  color: #333333;
  font-size: 0.875rem;
  font-weight: 400;
}

.footer__followers span {
  font-size: 1.125rem;
  font-weight: 700;
}

.footer__socials {
  display: flex;
  justify-content: flex-start;
  padding: 15px 0 30px 0;
}

.footer__desc {
  margin-right: 10px;
  color: #999999;
  font-family: "Roboto", sans-serif;
  font-size: 0.9375rem;
  font-weight: 300;
  font-style: italic;
  line-height: 24px;
}

.footer__icons {
  display: flex;
  justify-content: space-between;
}

.footer__icons svg {
  margin: 3px 7px;
  font-size: 1rem;
  color: #95e1d3;
  transition: all .2s linear;
}

.footer__icons svg:hover {
  color: #f38181;
}

.footer__title {
  margin: 25px 0 30px 0;
  color: #333333;
  font-size: 0.875rem;
  font-weight: 400;
  text-transform: uppercase;
}

.footer__copyright {
  width: 100%;
  padding: 20px 0;
  color: #333333;
  font-size: 0.875rem;
  font-weight: 400;
  border-top: 1px solid #e5e5e5;
  text-align: center;
}

.footer__copyright span {
  color: #f38181;
}

.subscribe {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 380px;
}

.subscribe__input {
  padding: 7px 65px 7px 13px;
  border: 1px solid #e7e7e7;
  background-color: #ffffff;
  color: #333333;
  font-family: "Roboto", sans-serif;
  font-size: 0.9375rem;
  font-weight: 300;
  font-style: italic;
  line-height: 24px;
}

.subscribe__input::placeholder {
  color: #cccccc;
}

.subscribe__btn {
  margin-left: -5px;
  padding: 11px 34px 12px 34px;
  border: 0;
  color: #ffffff;
  background-color: #95e1d3;
  font-size: 0.875rem;
  font-weight: 700;
  text-transform: uppercase;
  transition: all .2s linear;
}

.subscribe__btn:hover {
  background: #f38181;
}

.blogs {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.blogs__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.blogs__item:hover .blogs__title {
  text-decoration: underline;
  color: #f38181;
}

.blogs__image {
  padding-right: 20px;
}

.blogs__title {
  color: #333333;
  font-size: 0.75rem;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  transition: all .2s linear;
}

.blogs__date {
  color: #999999;
  font-family: "Roboto", sans-serif;
  font-size: 0.8125rem;
  font-weight: 300;
  font-style: italic;
  line-height: 24px;
}

.insta {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.insta__link {
  max-width: 32.3333%;
  height: auto;
  margin-bottom: 3px;
}

.insta__item {
  display: block;
  max-width: 100%;
  height: auto;
}

.insta__more {
  color: #333333;
  font-family: "Roboto", sans-serif;
  font-size: 0.8125rem;
  font-weight: 300;
  font-style: italic;
  line-height: 24px;
  transition: all .2s linear;
}

.insta__more:hover {
  color: #f38181;
  text-decoration: underline;
}

/*===================== Media =====================*/
/*==================== media ====================*/
@media (max-width: 1200px) {
  .reviews__text {
    font-size: 1.25rem;
  }
}

@media (max-width: 992px) {
  .logos__inner {
    justify-content: center;
  }
  .clients {
    justify-content: center;
    align-items: center;
  }
  .clients__item {
    width: 80%;
  }
  .clients__content {
    max-width: 100%;
  }
  .blog__date {
    top: 50%;
  }
  .footer__col--left {
    width: 100%;
  }
  .footer__col--middle, .footer__col--right {
    width: 50%;
  }
}

@media (max-width: 780px) {
  .nav__menu {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    display: none;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 2rem;
  }
}

@media (max-width: 780px) and (max-width: 1200px) {
  .nav__menu {
    font-size: calc(1.325rem + 0.9vw) ;
  }
}

@media (max-width: 780px) {
  .nav__button {
    width: 100%;
    font-size: 2rem;
    padding: 10px;
  }
}

@media (max-width: 780px) and (max-width: 1200px) {
  .nav__button {
    font-size: calc(1.325rem + 0.9vw) ;
  }
}

@media (max-width: 780px) {
  .nav--active .nav__menu {
    display: flex;
    justify-content: space-between;
    background: rgba(243, 129, 129, 0.9);
  }
  .menu__item {
    padding: 10px;
  }
  .slider-nav__text {
    display: none;
  }
  .card {
    margin-top: 30px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .card__item {
    padding: 15px;
    width: 100%;
  }
  .card__item:last-child {
    padding-bottom: 0;
  }
  .stat {
    justify-content: center;
  }
  .counter {
    flex: none;
    width: 33.3333%;
    padding: 10px 0;
    border-left: 0;
    border-bottom: 1px solid #b5eae0;
    border-right: 1px solid #b5eae0;
  }
  .counter:nth-child(3) {
    border-right: 0;
  }
  .counter:last-child {
    border-right: 0;
  }
  .services__item {
    width: 50%;
  }
  .services__item:nth-child(4) {
    border-bottom: 1px solid #e5e5e5;
  }
  .wedo {
    flex-direction: column;
    align-items: center;
  }
  .reviews__slide {
    flex-direction: column;
    align-items: center;
  }
  .reviews__text {
    font-size: 1rem;
  }
  .reviews__slide {
    padding: 30px;
  }
  .reviews__photo {
    margin-bottom: 30px;
  }
  .reviews__btn {
    top: 25%;
  }
  .reviews__review {
    width: 100%;
  }
  .works {
    flex-wrap: wrap;
    padding: 0 15px;
  }
  .works__column {
    flex: none;
    width: 50%;
  }
  .clients__item {
    width: 100%;
  }
  .blog {
    flex-direction: column;
  }
  .blog__item {
    width: 80%;
    align-items: center;
  }
  .blog__date {
    top: 60%;
  }
  .nav-burger {
    display: block;
  }
}

@media (max-width: 575px) {
  .services__item {
    width: 100%;
    border-bottom: 1px solid #e5e5e5;
  }
  .services__item:nth-child(5) {
    border-bottom: 1px solid #e5e5e5;
  }
  .wedo__item--photo {
    display: none;
  }
  .section--service {
    padding-bottom: 40px;
  }
  .footer__col--middle, .footer__col--right {
    width: 100%;
  }
  .blogs__item {
    justify-content: flex-start;
  }
  .subscribe {
    max-width: 100%;
  }
}

@media (max-width: 480px) {
  .header-wrap {
    min-height: 100%;
  }
  .slide__suptitle {
    font-size: 2rem;
  }
}

@media (max-width: 480px) and (max-width: 1200px) {
  .slide__suptitle {
    font-size: calc(1.325rem + 0.9vw) ;
  }
}

@media (max-width: 480px) {
  .slide__title {
    font-size: 3.75rem;
    line-height: 70px;
  }
}

@media (max-width: 480px) and (max-width: 1200px) {
  .slide__title {
    font-size: calc(1.5rem + 3vw) ;
  }
}

@media (max-width: 480px) {
  .slider-nav__btn--active::before {
    width: 40%;
  }
  .slider-nav__btn:hover::before {
    width: 40%;
  }
  .section {
    padding: 40px 0;
  }
  .section--design {
    height: 550px;
  }
  .section--service {
    padding: 80px 0 40px 0;
  }
  .section__header {
    margin: 0 auto 22px;
  }
  .section__title::after {
    margin: 13px auto 21px;
  }
  .design__ipad {
    max-width: 300px;
  }
  .design__iphone {
    max-width: 180px;
    bottom: -68px;
    left: 70%;
  }
  .stat {
    flex-direction: column;
  }
  .counter {
    flex: 1 1 0;
    width: 100%;
    border-left: 0;
    border-right: 0;
    border-bottom: 1px solid #b5eae0;
  }
  .counter:last-child {
    border-bottom: 0;
  }
  .services__item {
    margin-top: 25px;
    padding-bottom: 25px;
  }
  .works__column {
    width: 100%;
  }
  .reviews__slider {
    margin: 0 -15px;
  }
  .clients__item {
    flex-direction: column;
    align-items: center;
  }
  .clients__content {
    padding-left: 0;
  }
  .clients__title {
    text-align: center;
  }
  .clients__title::after {
    margin: 17px auto;
  }
  .blog__item {
    align-items: center;
    width: 100%;
  }
  .subscribe {
    justify-content: center;
    align-items: center;
  }
  .subscribe__input {
    width: 100%;
  }
  .subscribe__btn {
    margin-top: 15px;
  }
}
