// ====================  Default  ====================

p {
  hyphens: auto; }

button,
a,
input {
  outline: 0; }

button,
a {
  cursor: pointer; }

a {
  text-decoration: none; }

.icon {
  transition: fill ease-in-out .2s; }

*,
*:before,
*:after {
  box-sizing: border-box; }

html,
body {
  height: 100%;
  margin: 0;
  padding: 0; }

body {
  @include rfs(16);
  font-family: $main;
  color: $textcolor;
  line-height: 1.6; }

.container {
  width: 100%;
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  justify-content: center; }

